import { NextRequest, NextResponse } from 'next/server';
import { auth } from '@clerk/nextjs/server';
import { getInvoiceById } from '@/services/invoiceService';
import { createPaymentConfirmation } from '@/services/paymentService';
import { createStripePaymentLink } from '@/lib/stripe';
import { sendInvoiceEmail } from '@/lib/sendgrid';
import { db, Timestamp, COLLECTIONS } from '@/lib/firebase';
import { errors, handleApiError } from '@/utils/error';
import { checkRateLimit, ratelimit } from '@/lib/ratelimit';
import { logApiRequest, logApiResponse } from '@/utils/logger';
import { nanoid } from 'nanoid';

export const dynamic = 'force-dynamic';

/**
 * Send an invoice to the client
 * POST /api/invoices/[id]/send
 */
export async function POST(
  req: NextRequest,
  { params }: { params: Promise<{ id: string }> }
): Promise<NextResponse> {
  const { id } = await params;
  const startTime = Date.now();

  try {
    // 1. Authenticate
    const { userId } = await auth();
    if (!userId) {
      throw errors.unauthorized();
    }

    logApiRequest('POST', `/api/invoices/${id}/send`, userId);

    // 2. Rate limit check
    const { success, remaining, reset } = await checkRateLimit(userId, ratelimit);
    if (!success) {
      const retryAfter = Math.ceil((reset - Date.now()) / 1000);
      throw errors.rateLimit(`Rate limit exceeded. Try again in ${retryAfter} seconds`, reset);
    }

    // 3. Get invoice and verify ownership
    const invoice = await getInvoiceById(id, userId);

    // 4. Validate invoice status
    if (invoice.status !== 'draft') {
      return NextResponse.json(
        { error: 'INVALID_STATUS', message: 'Can only send draft invoices' },
        { status: 400 }
      );
    }

    // 5. Ensure payment methods are configured
    if (!invoice.paymentMethods || invoice.paymentMethods.length === 0) {
      return NextResponse.json(
        { error: 'NO_PAYMENT_METHODS', message: 'Invoice must have at least one payment method configured' },
        { status: 400 }
      );
    }

    // 6. Get user's bank details if bank_transfer is enabled
    let bankDetails: string | undefined;
    if (invoice.paymentMethods.includes('bank_transfer')) {
      const userDoc = await db.collection(COLLECTIONS.USERS).doc(userId).get();
      if (userDoc.exists) {
        const userData = userDoc.data();
        bankDetails = userData?.encryptedBankDetails;
        if (!bankDetails) {
          return NextResponse.json(
            {
              error: 'BANK_DETAILS_REQUIRED',
              message: 'Bank details must be configured to use bank transfer payment method'
            },
            { status: 400 }
          );
        }
      }
    }

    // 7. Create payment confirmation record
    const confirmation = await createPaymentConfirmation(
      invoice.invoiceId,
      userId,
      invoice.clientEmail,
      invoice.amount
    );

    // 8. Create Stripe payment link if card payment is enabled
    let stripePaymentLink: string | undefined;
    if (invoice.paymentMethods.includes('card')) {
      stripePaymentLink = await createStripePaymentLink({
        invoiceReference: invoice.reference,
        amount: invoice.amount,
        currency: invoice.currency || 'GBP',
        freelancerId: userId,
        clientEmail: invoice.clientEmail,
      });
    }

    // 9. Build confirmation URL
    const confirmationUrl = `${process.env.NEXT_PUBLIC_APP_URL}/confirmation/${confirmation.confirmationToken}`;

    // 10. Get freelancer details for email
    const userDoc = await db.collection(COLLECTIONS.USERS).doc(userId).get();
    const freelancerName = userDoc.exists ? userDoc.data()?.fullName || 'Freelancer' : 'Freelancer';

    // 11. Send email via SendGrid
    await sendInvoiceEmail({
      toEmail: invoice.clientEmail,
      freelancerName,
      invoiceReference: invoice.reference,
      amount: invoice.amount,
      currency: invoice.currency || 'GBP',
      dueDate: invoice.dueDate.toDate(),
      description: invoice.description,
      bankDetails,
      stripeLink: stripePaymentLink,
      confirmationUrl,
    });

    // 12. Update invoice status to 'sent'
    await db.collection(COLLECTIONS.INVOICES).doc(invoice.invoiceId).update({
      status: 'sent',
      sentAt: Timestamp.now(),
      paymentConfirmationId: confirmation.confirmationId,
      stripePaymentLink,
      updatedAt: Timestamp.now(),
    });

    // 13. Create email_sent record
    await db.collection(COLLECTIONS.EMAILS_SENT).add({
      emailId: nanoid(),
      userId,
      invoiceId: invoice.invoiceId,
      type: 'invoice',
      recipient: invoice.clientEmail,
      sendgridMessageId: '', // SendGrid will provide this via webhook
      status: 'sent',
      sentAt: Timestamp.now(),
      createdAt: Timestamp.now(),
    });

    // 14. Log response
    const duration = Date.now() - startTime;
    logApiResponse('POST', `/api/invoices/${id}/send`, 200, duration, userId);

    return NextResponse.json({
      success: true,
      message: 'Invoice sent successfully',
      invoice: {
        invoiceId: invoice.invoiceId,
        reference: invoice.reference,
        status: 'sent',
        sentAt: new Date().toISOString(),
      },
      confirmation: {
        confirmationId: confirmation.confirmationId,
        confirmationUrl,
      },
      stripePaymentLink,
      remainingRequests: remaining,
    });
  } catch (error) {
    const duration = Date.now() - startTime;
    logApiResponse('POST', `/api/invoices/${id}/send`, 500, duration);
    return handleApiError(error);
  }
}
