import Stripe from 'stripe';

// Lazy initialization to avoid build-time errors
let stripeInstance: Stripe | null = null;

function getStripe(): Stripe {
  if (!stripeInstance) {
    const apiKey = process.env.STRIPE_SECRET_KEY;
    if (!apiKey) {
      throw new Error('STRIPE_SECRET_KEY is not configured');
    }
    stripeInstance = new Stripe(apiKey, {
      // Using latest stable API version - Stripe will default to account version if omitted
      // Specify explicit version only when needed for compatibility
      typescript: true,
    });
  }
  return stripeInstance;
}

// Export for backward compatibility
export const stripe = new Proxy({} as Stripe, {
  get(target, prop) {
    return (getStripe() as any)[prop];
  }
});

// Create payment link for invoice
export async function createStripePaymentLink(params: {
  amount: number;
  invoiceReference: string;
  clientEmail: string;
  freelancerId: string;
  currency?: string;
}): Promise<string> {
  try {
    const paymentLink = await stripe.paymentLinks.create({
      line_items: [
        {
          price_data: {
            currency: (params.currency || 'GBP').toLowerCase(),
            unit_amount: Math.round(params.amount * 100), // Convert to pence
            product_data: {
              name: `Invoice ${params.invoiceReference}`,
              description: `Payment for ${params.invoiceReference}`,
            },
          },
          quantity: 1,
        },
      ],
      after_completion: {
        type: 'redirect',
        redirect: {
          url: `${process.env.NEXT_PUBLIC_APP_URL}/payment-success?invoice=${params.invoiceReference}`,
        },
      },
      metadata: {
        invoiceReference: params.invoiceReference,
        freelancerId: params.freelancerId,
        clientEmail: params.clientEmail,
      },
      // Allow promotion codes
      allow_promotion_codes: true,
    });

    return paymentLink.url;
  } catch (error) {
    console.error('Stripe payment link creation error:', error);
    throw new Error('Failed to create payment link');
  }
}

// Verify webhook signature (CRITICAL for security)
export function verifyWebhookSignature(
  payload: string,
  signature: string,
  secret: string
): Stripe.Event {
  try {
    return stripe.webhooks.constructEvent(payload, signature, secret);
  } catch (error) {
    console.error('Webhook signature verification failed:', error);
    throw new Error('Invalid webhook signature');
  }
}

// Get payment intent details
export async function getPaymentIntent(paymentIntentId: string) {
  try {
    return await stripe.paymentIntents.retrieve(paymentIntentId);
  } catch (error) {
    console.error('Stripe payment intent retrieval error:', error);
    throw new Error('Failed to retrieve payment intent');
  }
}

// Create customer (for subscription management later)
export async function createCustomer(params: {
  email: string;
  name: string;
  metadata?: Record<string, string>;
}) {
  try {
    return await stripe.customers.create({
      email: params.email,
      name: params.name,
      metadata: params.metadata,
    });
  } catch (error) {
    console.error('Stripe customer creation error:', error);
    throw new Error('Failed to create customer');
  }
}
